<template>
  <HeaderNav />
  <News v-if="numState == 1" />
  <News2 v-if="numState == 2" />
  <News3 v-if="numState == 3" />
  <div class="page_btn_box">
    <!-- <v-icon @click="prevBtn" class="btn_left page_btn">mdi-chevron-left</v-icon> -->
    <span v-for="index in btnNum" :key="index" style="align-self: center">
      <span
        @click="clickBtn(index)"
        :class="'btn_num' + activeClass"
        style="margin: 6px"
        >{{ index }}</span
      >
    </span>
    <!-- <v-icon @click="nextBtn" class="btn_right page_btn"
      >mdi-chevron-right</v-icon
    > -->
  </div>
  <Footer />
</template>

<script>
import HeaderNav from "@/components/header/SubNav.vue";
import News from "@/components/sub/menu_news/news.vue";
import News2 from "@/components/sub/menu_news/news2.vue";
import News3 from "@/components/sub/menu_news/news3.vue";
import Footer from "@/components/footer/Footer.vue";
export default {
  components: {
    HeaderNav,
    News,
    Footer,
    News2,
    News3,
  },
  data() {
    return {
      btnNum: [1, 2, 3],
      numState: 1,
      maxbtnNum: 3,
      minbtnNum: 1,
      activeClass: "",
    };
  },
  methods: {
    clickBtn(index) {
      this.numState = index;
      // if (this.numState == index) {
      //   this.activeClass = "--active";
      // } else {
      //   this.activeClass = "";
      // }
    },
    nextBtn() {
      if (this.numState >= this.maxbtnNum) {
        this.numState = this.maxbtnNum;
        return;
      }
      this.numState += 1;
    },
    prevBtn() {
      if (this.numState <= this.minbtnNum) {
        this.numState = this.minbtnNum;
        return;
      }
      this.numState -= 1;
    },
  },
};
</script>

<style lang="scss" scoped>
.page_btn_box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 5%;
  .page_btn {
    margin: 0% 0.5%;
    cursor: pointer;
  }
  .btn_num {
    margin: 0% 0.5%;
    font-family: "Pretendard-Regular";
    color: #585050;
    font-weight: bold;
    cursor: pointer;
  }
}
// PC
@media screen and (min-width: 1080px) {
  .btn_num {
    font-size: 16px;
  }
  .page_btn {
    font-size: 20px;
  }
}

// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .btn_num {
    font-size: 14px;
  }
  .page_btn {
    font-size: 18px;
  }
}

// 테블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .btn_num {
    font-size: 14px;
  }
  .page_btn {
    font-size: 18px;
  }
}

// 모바일
@media screen and (min-width: 320px) and (max-width: 639px) {
  .btn_num {
    font-size: 10px;
  }
  .page_btn {
    font-size: 12px;
  }
}
</style>
